<script>
export default {
  name: "Lab1Vue",
  data: () => ({
    tab: null,
    code1:
      "(define (sum-of-digits-regular n)\n" +
      "  (if (= n 0)\n" +
      "      0\n" +
      "      (+ (modulo n 10) (sum-of-digits-regular (quotient n 10)))))\n" +
      "\n" +
      "(define (sum-of-digits-tail-rec n)\n" +
      "  (define (helper n acc)\n" +
      "    (if (= n 0)\n" +
      "        acc\n" +
      "        (helper (quotient n 10) (+ acc (modulo n 10)))))\n" +
      "  (helper n 0))\n" +
      "\n" +
      "\n" +
      '(display "Введіть натуральне число: ")\n' +
      "(define n (read))\n" +
      "\n" +
      '(display "Сума цифр числа (звичайна рекурсія): ")\n' +
      "(display (sum-of-digits-regular n))\n" +
      "(newline)\n" +
      "\n" +
      '(display "Сума цифр числа (хвостова рекурсія): ")\n' +
      "(display (sum-of-digits-tail-rec n))\n" +
      "(newline)\n",
    code2:
      "(define (calculate-deposit sum pr m)\n" +
      "  (if (= m 0)\n" +
      "      sum\n" +
      "      (calculate-deposit (* sum (+ 1 (/ pr 100))) pr (- m 1))))\n" +
      "\n" +
      '(display "Введіть суму вкладу: ")\n' +
      "(define sum (read))\n" +
      "\n" +
      '(display "Введіть відсотки за період: ")\n' +
      "(define pr (read))\n" +
      "\n" +
      '(display "Введіть кількість періодів: ")\n' +
      "(define m (read))\n" +
      "\n" +
      '(display "Величина вкладу по звершенні m періодів часу: ")\n' +
      "(display (calculate-deposit sum pr m))\n" +
      "(newline)\n",
  }),
};
